const find_gok = (year_chi, month_chi, day, time_chi) => {
    // console.log(`year_chi = ${year_chi} month_chi=${month_chi} day=${day} time_chi=${time_chi}`);

    let cnt = 0
    cnt += floor12_int[floor12_str.indexOf(year_chi)]
    // 
    cnt += floor12_int[floor12_month_str.indexOf(month_chi)]
    // 
    cnt += day
    // 
    cnt += floor12_int[floor12_str.indexOf(time_chi)]
    // 


    let gok = cnt % 9
    gok = gok == 0 ? 9 : gok

    return gok
}

const floor_tin_gone = (number, up_down) => {
    let floor_seq_array = []
    let cnt = 1
    floor_seq_array[0] = number

    if (up_down == 0) {
        while (cnt <= 8) {

            let val = floor_seq_array[cnt - 1] - 1
            if (val < 1) {
                val = 9
            }

            floor_seq_array[cnt] = val
            cnt++
        }
    }
    else if (up_down == 1) {
        while (cnt <= 8) {

            let val = floor_seq_array[cnt - 1] + 1
            if (val > 9) {
                val = 1
            }

            floor_seq_array[cnt] = val
            cnt++
        }
    }

    return floor_seq_array
}

const find_empty_and_chun = (time_gone_chi) => {
    let gone = time_gone_chi[0]
    let chi = time_gone_chi[1]


    let end = 9 - tin_gone_10_str.indexOf(gone)

    let empty1 = end + 1 + floor12_str.indexOf(chi)
    let empty2 = end + 2 + floor12_str.indexOf(chi)

    empty1 = empty1 >= 12 ? empty1 - 12 : empty1
    empty2 = empty2 >= 12 ? empty2 - 12 : empty2


    let empty = {
        "empty1": floor12_str[empty1],
        "empty2": floor12_str[empty2],
        "chun_gone": find_chun(empty2),
        "chun_chi": find_chun_chi(empty2)
    }

    return empty
}

