const find_yan_gone = (tin_tin_gone_seq, time_gone, time_chi, door_tin_gone, chun_gone, yinyang, door_gong_idx) => {

    let yan_gone_array = []
    yan_gone_array.push(...tin_tin_gone_seq)
    let yan_gone_obj_array = []

    if (time_gone == "") {
        let gone = ""
        gak_time_gone.forEach((g) => {
            if (g['gak'] == time_chi)
                gone = g['gone']
        })


        time_gone = gone
    }



    if (door_tin_gone.indexOf(time_gone) > -1) {
        let shifted_tin_gone_seq = []
        shifted_tin_gone_seq.push(...tin_gone_seq)
        let shift_cnt = 0
        shifted_tin_gone_seq.forEach((t, idx) => {
            if (t.indexOf(chun_gone) > -1) {

                shift_cnt = idx
            }
        })

        // shifted_tin_gone_seq = shiftLeft(shifted_tin_gone_seq, shift_cnt)
        shifted_tin_gone_seq = shiftArray(shifted_tin_gone_seq, chun_gone)


        if (yinyang == 0) {
            yan_gone_array[3] = shifted_tin_gone_seq[1]
            yan_gone_array[2] = shifted_tin_gone_seq[2]
            yan_gone_array[5] = shifted_tin_gone_seq[3] + shifted_tin_gone_seq[0]
            yan_gone_array[0] = shifted_tin_gone_seq[4]
            yan_gone_array[4] = shifted_tin_gone_seq[5]
            yan_gone_array[1] = shifted_tin_gone_seq[6]
            yan_gone_array[6] = shifted_tin_gone_seq[7]
            yan_gone_array[7] = shifted_tin_gone_seq[8]
        }
        else if (yinyang == 1) {
            yan_gone_array[7] = shifted_tin_gone_seq[1]
            yan_gone_array[6] = shifted_tin_gone_seq[2]
            yan_gone_array[1] = shifted_tin_gone_seq[3]
            yan_gone_array[4] = shifted_tin_gone_seq[4]
            yan_gone_array[0] = shifted_tin_gone_seq[5]
            yan_gone_array[5] = shifted_tin_gone_seq[6] + shifted_tin_gone_seq[0]
            yan_gone_array[2] = shifted_tin_gone_seq[7]
            yan_gone_array[3] = shifted_tin_gone_seq[8]
        }
    }
    else {


        let shift_cnt = 0;
        let time_gone_idx = 0
        // yan_gone_array.indexOf(time_gone)
        yan_gone_array.forEach((y, idx) => {
            if (y.indexOf(time_gone) > -1)
                time_gone_idx = idx
        })

        if (time_gone_idx > door_gong_idx) {
            shift_cnt = time_gone_idx - door_gong_idx
            yan_gone_array = shiftLeft(yan_gone_array, shift_cnt)
        }
        else if (door_gong_idx > time_gone_idx) {
            shift_cnt = door_gong_idx - time_gone_idx
            yan_gone_array = shiftRight(yan_gone_array, shift_cnt)
        }

    }

    yan_gone_array.forEach((y) => {
        let gone = y.split('')
        if (gone.length == 1) {
            yan_gone_obj_array.push({ primary: gone[0], second: "" })
        }
        else if (gone.length == 2) {
            yan_gone_obj_array.push({ primary: gone[0], second: gone[1] })
        }
    })



    return yan_gone_obj_array
}

