    let floor_tin_gone5 = dataSetObj.display_floor_tin_gone_array[8]
    let temp_floor_tin_gone_array = dataSetObj.display_floor_tin_gone_array.filter((t, idx) => idx != 8)
    temp_floor_tin_gone_array = shiftLeft(temp_floor_tin_gone_array, n)
