const spinGone = (left, right) => {
    if (left > 0)
        return shiftRight(door_sun_seq, left)
    else if (right > 0)
        return shiftLeft(door_sun_seq, right)
    else
        return door_sun_seq
}

const shiftLeft = (arr, n) => {
    let temp_array = []
    temp_array.push(...arr)
    for (let i = 0; i < n; i++) {
        let temp = temp_array.shift();
        temp_array.push(temp);
    }
    return temp_array;
}

const shiftRight = (arr, n) => {
    let temp_array = []
    temp_array.push(...arr)
    for (let i = temp_array.length; i > n; i--) {
        let temp = temp_array.shift();
        temp_array.push(temp);
    }
    return temp_array;
}

const shiftArray = (arr, target) => {
    return arr.concat(arr.splice(0, arr.indexOf(target)));
}

